import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { ItemMasterService } from "../../service/itemMaster.service";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { ColorTypeService } from "app/modules/design/service/colorType.service";

@Component({
  selector: "app-detail-item-master",
  templateUrl: "./detail-item-master.component.html",
  styleUrls: ["./detail-item-master.component.scss"],
})
export class DetailItemMasterComponent implements OnInit {
  public contentHeader: Object;
  public ItemMasterForm: FormGroup;
  public submitted = false;
  public editable = false;
  public id;
  public getColors;

  constructor(
    private _sw: SweetalertComponent,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private _serviceData: ItemMasterService,
    private _colorService: ColorTypeService,
    private _router: Router
  ) {}

  getAllColors() {
    this._colorService.getAll().subscribe((res)=>{
      if(res.data){
        this.getColors = res.data
      }
    })
    ;
  }

  onSubmit(formData) {

    this.submitted = true;
    if (this.ItemMasterForm.invalid) {
      return;
    }

    if (this.editable) {
      this.updateFormDetail(formData);
    } else {
      this.insertFormDetail(formData);
    }
  }

  insertFormDetail(formData) {
    this._serviceData.create(formData).subscribe(
      (res) => {
        this._sw.fire("success", "Item Master", `Record Successfully Add..`);
        this.submitted = false;
        this.ItemMasterForm.reset();
      },
      (err) => {
        this._sw.fire("error", "Item Master", err.error.message);
      }
    );
  }

  updateFormDetail(formData) {
    this._serviceData.update({ id: this.id, ...formData }).subscribe(
      (res) => {
        this._sw.fire("success", "Item Master", `Record Successfully Update..`);
        this.editable = false;
        this.submitted = false;
        this._router.navigate(["/item-master"]);
      },
      (err) => {
        this._sw.fire("error", "Item Master", err.error.message);
      }
    );
  }

  ngOnInit(): void {
    this.id = +this.route.snapshot.paramMap.get("id");

    if (this.id > 0) {
      this.getDetailById();
    }
    this.makeReactiveForm();
    this.getAllColors();

    this.contentHeader = {
      headerTitle: "Design",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Item Master",
            isLink: true,
            link: "/",
          },
          {
            name: "Form",
            isLink: false,
          },
        ],
      },
    };
  }

  get f() {
    return this.ItemMasterForm.controls;
  }

  getDetailById() {
    this._serviceData.getDetailById({ id: this.id }).subscribe((res) => {
      this.editable = true;
      this.ItemMasterForm.patchValue({
        shortName: res.data.shortName,
        name: res.data.name,
        itemType: res.data.itemType,
        UOM: res.data.UOM,
        article: res.data.article,
        color: res.data.color,
        closingQuantity:res.data.closingQuantity,
        status: res.data.status,
      });
    });
  }

  makeReactiveForm() {
    this.ItemMasterForm = this.fb.group({
      name: ["", [Validators.required]],
      shortName: [null],
      itemType: ["Stich", [Validators.required]],
      UOM: ["No"],
      article: [null],
      color: [null],
      closingQuantity: [null],
      status: [null],
    });
  }
}
