import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListItemMasterComponent } from './components/list-item-master/list-item-master.component';
import { DetailItemMasterComponent } from './components/detail-item-master/detail-item-master.component';
import { RouterModule, Routes } from '@angular/router';
import { ContentHeaderModule } from 'app/layout/components/content-header/content-header.module';
import { CoreCommonModule } from '@core/common.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SweetalertComponent } from 'app/shared/sweetalert/sweetalert.component';

const routes: Routes = [
  {
    path: "",
    component: ListItemMasterComponent,
    data: { animation: "item-master" },
  },
  {
    path: "list",
    component: ListItemMasterComponent,
    data: { animation: "item-list-master" },
  },
  {
    path: "form",
    component: DetailItemMasterComponent,
    data: { animation: "item-detail-master" },
  },
  {
    path: "form/:id",
    component: DetailItemMasterComponent,
    data: { animation: "item-detail-master-id" },
  }

]

@NgModule({
  declarations: [
    ListItemMasterComponent,
    DetailItemMasterComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ContentHeaderModule,
    CoreCommonModule,
    FormsModule,
    ReactiveFormsModule,
  ],
  providers: [SweetalertComponent],
})
export class ItemMasterModule { }
