import { Component, OnInit } from '@angular/core';
import { SweetalertComponent } from 'app/shared/sweetalert/sweetalert.component';
import { ItemMasterService } from '../../service/itemMaster.service';

@Component({
  selector: 'app-list-item-master',
  templateUrl: './list-item-master.component.html',
  styleUrls: ['./list-item-master.component.scss']
})
export class ListItemMasterComponent implements OnInit {
  public contentHeader: Object;
  public tableDataArray = [];

  constructor(
    private _sw: SweetalertComponent,
    private _serviceData: ItemMasterService,
  ) { }

  ngOnInit(): void {
    this.getAllDetail();


    this.contentHeader = {
      headerTitle: "Design",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Item Master",
            isLink: true,
            link: "/",
          },
          {
            name: "List",
            isLink: false,
          },
        ],
      },
    };
  }


  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      }else if(!res.data && this.tableDataArray.length == 1){
        this.tableDataArray= []
      }
    });
  }

  confirmDelete(id) {
    let deleteId = {
      id: id,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "Collection", "There is a problem");
        }
      )
    );
  }


  searchTerm: string = '';
 get filterTableData() {
    if (!this.searchTerm) {
      return this.tableDataArray;
    }
    const searchTermLowerCase = this.searchTerm.toLowerCase();

    return this.tableDataArray.filter(
      (item) =>
        item.shortName.toLowerCase().includes(searchTermLowerCase) ||
        item.name.toLowerCase().includes(searchTermLowerCase) 
    );
  }

}
